.blog_box {
    position: relative;
}

.blog_box_prep {
    position: absolute;
    top: 5rem;
    left: 50%;
    transform: translateX(-50%);
    display: inline-block;
    font-family: 'Gruppo', sans-serif;
    font-weight: bold;
    padding: 1.5rem 3rem;
    font-size: 3rem;
    border-radius: 7rem;
    border: rgba(220, 20, 60, 0.66) 2px solid;
    color: rgba(220, 20, 60, 0.80);
    box-shadow: 0 0 16px rgba(220, 20, 60, 0.80);
}

.blog_item {
    margin: 32px 0;
    padding: 36px 48px;
    border-radius: 30px;
    box-shadow: 5px 5px 20px rgb(19, 19, 19),
    -5px -5px 20px rgb(79, 79, 79);
    color: white;
    opacity: 0.2;
}

.blog_title {
    font-size: 1.4rem;
}

.blog_discrp {
    font-size: 1.2rem;
}

.blog_tags {
    text-align: right;
    margin-bottom: 1rem;
}

.blog_tags span {
    margin-left: 8px;
    border-radius: 8px;
    padding: 8px 24px;
    background: #797979;
}

.blog_date {
    text-align: right;
}